<template>
    <div class="companyParticulars">
        <div class="header">
            <div class="return">
                <div @click="routerBack" class="return-img"></div>
                <div class="more"></div>
            </div>
            <div class="title">
                <div class="position-title">{{data.companyMessage.companyTitle}}</div>
                <div class="position-money">{{data.companyMessage.companyMoney}}</div>
                <div class="position-need"><span v-for="need in data.companyMessage.companyClaim">{{need}}</span></div>
            </div>
            <div class="fuli">
                <span v-for="fuli in data.companyMessage.companyWelfare">{{fuli}}</span>
                <span>五险一金</span>
            </div>
        </div>
        <div class="main">
            <div class="content">
                <div class="main-title">
                    <div><span class="i-blue"></span>岗位描述<span class="zhaoPin">招聘中</span></div>
                </div>
                <div class="artical">
                    <p v-for="item in minuteContent">{{item}}</p>
                </div>
            </div>
        </div>
        <div class="footer">
            <div class="fenxiang">
                <span class="icon i-fenxiang"></span>
                <span>分享</span>
            </div>
            <div class="shoucang">
                <span class="icon i-shoucang"></span>
                <span>收藏</span>
            </div>
            <div class="touidjianli">
                <span>投递简历</span>
            </div>
        </div>
    </div>

</template>
<script type="text/javascript">
    export default {
        props: {
            reqdata: {
                type: Array,
                default: []
            }
        },
        data() {
            return {
                minuteContent: [],
                data:{}
            }
        },
        methods: {
            routerBack() {
                this.$router.go(-1);
            }
        },
        created() {
            this.data = this.reqdata[this.$route.params.id];
            this.minuteContent = this.reqdata[this.$route.params.id].minuteContent;
        }
    }
</script>
<style scoped>
    .companyParticulars{
        width: 100%;
    }
    .header{
        width: 100%;
    }
    .header:after{
        content: '';
        display: block;
        width: 100%;
        background-color: #f8f8fa;
        height: 10px;
    }
    .return{
        width: 100%;
        height: 60px;
        position: relative;
    }
    .return-img,.more{
        width: 30px;
        height: 30px;
        margin-left: 20px;
        position: absolute;
        margin-top: -15px;
        top:50%;
    }
    .return-img{
        margin-left: 10px;
        background: url("button-left-return.png") no-repeat;
        background-size: 30px 30px;
    }
    .more{
        right: 20px;
        background: url("more1.png") no-repeat;
        background-size: 30px 30px;
    }
    .title{
        overflow: hidden;
        letter-spacing: 1px;
        width: 100%;
    }
    .position-title{
        padding-left: 16px;
        line-height: 16px;
        font-size: 16px;
        font-weight: 700;
        margin: 10px 0;
    }
    .position-money{
        padding-left: 16px;
        line-height: 16px;
        font-size: 16px;
        font-weight: 700;
        color: red;
        margin: 10px 0;
    }
    .position-need{
        padding-left: 16px;
        color: #8c8c8c;
        font-size: 12px;
        margin:10px 0;
    }
    .position-need span{
        margin-right: 5px;
    }
    .title:after{
        position: absolute;
        content: '';
        display: block;
        width: 100%;
        border-bottom: 1px solid rgb(219, 219, 219);
    }
    @media (-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio:2){
        .title:before{
            transformX:scale(0.5);
        }
    }
    .fuli {
        margin:10px 0 10px 16px;
        width: 100%;
        height: 10px;
        line-height: 12px;
        font-size: 12px;
        color: #8c8c8c;
    }
    .fuli span {
        margin-right: 5px;
    }
/*////////////////////////*/
    .content{
        margin: 15px;
    }
    .main{
        padding-bottom: 50px;
    }
    .main-title{
        width: 100%;
        height: 16px;
        font-weight: 700;
        font-size: 16px;
    }
    .i-blue{
        display: inline-block;
        width: 3px;
        height: 16px;
        background-color: #198afa;
        border-radius: 3px;
        margin-right: 10px;
    }
    .fenxiang{
        position: relative;
    }
    .fenxiang:after{
        position: absolute;
        content: '';
        display: block;
        height: 100%;
        top: 0;
        right: 0;
        border-right:  1px solid rgb(219, 219, 219);
    }
    @media (-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio:2){
        .fenxiang:before{
            transformY:scale(0.5);
        }
    }
    .zhaoPin{
        display: inline-block;
        float: right;
        color: #8c8c8c;
        font-weight: 200;
        font-size: 12px;
    }
    .artical{
        color: #7b7b7b;
        font-size: 14px;

    }
    .artical p{
        margin:10px 0;
        line-height: 20px;
    }





    .footer{
        position: fixed;
        bottom: 0;
        left: 0;
        display: flex;
        text-align: center;
        width: 100%;
        height:45px;
        font-size: 12px;
        line-height: 12px;
        background-color: #fff;
    }
    .fenxiang, .shoucang{
        flex: 1;
    }
    .touidjianli{
        flex: 2;
        font-size: 16px;
        line-height: 45px;
        color: white;
        background-color: #1a8afa;
    }
    .icon{
        margin: 5px auto;
        display: block;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-size: 20px 20px;
    }
    .i-fenxiang{
        background-image: url(./footer-fenxiang.png)
    }
    .i-shoucang{
        background-image: url(more1.png)
    }
</style>


