<template>

    <transition name="fade" mode="out-in">
        <router-view :reqdata="reqdata"></router-view>
    </transition>
</template>
<script>
    export default {
        name: 'index',
        data () {
            return {
                reqdata: ''
            }
        },
        components: {
        },
        methods:{
        },
        beforeCreate() {
            this.$http.get("./static/data.json").then(function(res){
                this.reqdata =  (JSON.parse(res.bodyText)).zhiWei.companies;
            })
        }
    }
</script>
<style>
    html,body,.app{
    height: 100%;
    }
    body{font-family:Helvetica;font-weight: 200}
    body::-webkit-scrollbar{
    width:0;
    height:0;
    }
    a{
    color: rgb(219, 219, 219);
    text-decoration: none;
    }
    p{
    padding: 0;
    margin: 0;
    }
    a:hover{
    color: rgb(219, 219, 219);
    }
    ul{
    padding: 0;
    margin: 0;
    }
    li{
    list-style: none;
    }
    input{
    background:none;
    outline:none;
    border:0;
    }
    .fade-enter-active{
        -webkit-animation: move-out .2s;;
        animation: move-in .2s;
    }
    /*消失*/
    .fade-leave-active {
        -webkit-animation: move-out .2s;;
        animation: move-out .2s;
    }
    @keyframes move-out {
        0% {
            transform: translateX(0);
            opacity: 1;
        }
        100% {
            transform: translateX(-100%);
            opacity: 1;
        }
    }
    @keyframes move-in {
        0% {
            transform: translateX(100%);
        }
        100% {
            transform: translateX(0);
        }
    }
</style>


