import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Index from './index.vue'
import zhiWei from './components/zhiWei/zhiWei.vue'
import message from './components/message/message.vue'
import resume from './components/resume/resume.vue'
import mine from './components/mine/mine.vue'
import companyParticulars from './components/zhiWei-companyParticulars/companyParticulars.vue'
Vue.use(VueRouter);
Vue.use(VueResource);
const router =new VueRouter({
  routes: [
    {path: '/companyParticulars/:id', component: companyParticulars},
    {path: '/App', component: App, children: [
      { path: 'zhiWei', component: zhiWei},
      { path: 'message', component: message},
      { path: 'resume', component: resume},
      { path: 'mine', component: mine}
      ]
    }
  ]
});
new Vue({
  el: '#app',
  // 必须要有 template
  template: '<Index/>',
  components:{
    Index
  },
  propsData:{
    msg: '你好'
  },
  router,
  created(){
  }
});
router.push('/App/zhiWei');


