<template>
    <div class="slide-show">
        <div class="slide-img">
            <a href="javascript:void(0)">
                <transition name="move-in">
                    <img v-if="isShow" :src="slides[nowIndex].src">
                </transition>
                <transition name="move-out">
                    <img v-if="!isShow" :src="slides[nowIndex].src">
                </transition>
            </a>
        </div>
        <ul class="slide-pages">
            <li @click="goto(prevIndex)">&lt;</li>
            <li v-for="(item, index) in slides"
                @click="goto(index)"
            >
                <a :class="{on: index === nowIndex}">{{ index + 1 }}</a>
            </li>
            <li @click="goto(nextIndex)">&gt;</li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'slide-show',
        props: {
            slides: {
                type: Array,
                default: []
            },
            inv: {
                type: Number,
                default: 1000
            }
        },
        data () {
            return {
                nowIndex: 0,
                isShow: true
            }
        },
        computed: {
            prevIndex () {
                if (this.nowIndex === 0) {
                    return this.slides.length - 1
                }
                else {
                    return this.nowIndex - 1
                }
            },
            nextIndex () {
                if (this.nowIndex === this.slides.length - 1) {
                    return 0
                }
                else {
                    return this.nowIndex + 1
                }
            }
        },
        methods: {
            goto (index) {
                var _this =this;
                _this.isShow = false;
                setTimeout(function () {
                    _this.isShow = true;
                    _this.nowIndex = index
                 }, 10)
            },
            runInv () {
                var _this = this;
                _this.invId = setInterval(function(){
                    _this.goto(_this.nextIndex)
            }, _this.inv)
            },
            clearInv () {
                clearInterval(this.invId)
            }
        },
        mounted () {
            window.a = this;
            this.runInv();
        }
    }
</script>
<style scoped>
    .slide-show{
        position: relative;
        width: 100%;
        height: 185px;
        overflow: hidden;
    }
    .slide-show img{
        display: block;
        height: 185px;
        position: absolute;
        top: 0;
    }
    .slide-img{
        width: 100%;
        height: 185px;
    }
    .move-in-enter-active {
        animation: move-in 2s
    }

    .move-out-leave-active {
        animation: move-out 2s
    }
    @keyframes move-out {
        0% {
            transform: translateX(0);
        }
        100% {
            transform: translateX(-100%);
        }
    }
    @keyframes move-in {
        0% {
            transform: translateX(100%);
        }
        100% {
            transform: translateX(0);
        }
    }
</style>


