<template>
 <div class="message">
   <div class="header">
     <div class="title">消息</div>
      <div class="jinZhan">
        <div class="item" >
            <div class="content">
                <div><img src="../../static/img/touzhijinzhan.png" alt=""></div>
                <div class="content-title">投递进展</div>
            </div>
        </div>
        <div class="item">
            <div class="content">
                <div><img src="../../static/img/mianshiyaoqing.png" alt=""></div>
                <div class="content-title">面试邀请</div>
            </div>
        </div>
        <div class="item" >
            <div class="content">
                <div><img src="../../static/img/shuiknawo.png" alt=""></div>
                <div class="content-title">谁看了我</div>
            </div>
        </div>
      </div>
   </div>
   <div class="main">
       <div class="content">
           <div class="mishu"><div ><img class="mishu-img" src="../../static/img/秘书.png" alt=""></div><div class="mishu-text">智联小秘书</div></div>
       </div>
     </div>
 </div>
</template>

<script type="text/ecmascript-6">
 export default {
  data() {
    return {
    }
  },
  components: {

  }
 }
</script>

<style scoped >
.message{
    height: 100%;
}
.title{
  width: 100%;
  text-align: center;
  height: 75px;
  line-height: 75px;
  font-weight: 200;
  font-size:20px;
}
.title:after{
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    border-top: 1px solid rgb(219, 219, 219);
}
@media (-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio:2){
    .title:after{
