    font-size: 16px;
    font-weight: 400;
}
.main:before{
    content: "";
    display: block;
    width: 100%;
    background-color: #efeff4;
    height: 15px;
}
.main .content{
    margin: 15px 0 0 10px;
}
.mishu{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
}
.mishu>div{

}
.mishu-img{
    width: 50px;
    height: 50px;
}
.mishu-text{
    width: 100%;
    margin-top: 15px;
    margin-left: 20px;
}
.mishu-text:after{
    content: '';
    display: block;
    width: 100%;
    border-bottom: 1px solid #f4f4f4;
    padding-bottom: 24px;
}
@media (-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio:2){
  .mishu-text:after{
    transform:scaleY(0.5); 
  }
}
</style>


