<template>
	<div class="zhiWei">
		<div class="header">
			<slide-show :slides="slides" :inv="invTime"></slide-show>
			<input type="text" class="input-search" placeholder="搜索职位/公司">
			<div class="saoYiao"></div>
		</div>
		<div class="school-zhaoPin">
			<div class="content border-1px-right">
				<div class="school-img"><img width="55px" height="55px" src="../../static/img/大学生.png" alt=""></div>
				<div class="school-message">
					<div class="school-title">校园招聘</div>
					<div class="school-remarks">大学生求职专属</div>
				</div>
			</div>
			<!------>
			<div class="content">
				<div class="school-img"><img width="55px" height="55px" src="../../static/img/大学生2.png" alt=""></div>
				<div class="school-message">
					<div class="school-title">面试直通车</div>
					<div class="school-remarks">直接与企业约面试</div>
				</div>
			</div>
		</div>
		<div class="main">
			<ul class="company-message">
					<router-link tag="li" v-if="data" class="company-content" v-for="(item,index) of data"  :to="/companyParticulars/+index" :key="index">
						<div class="company-title">{{item.companyMessage.companyTitle}}<span class="company-money">{{item.companyMessage.companyMoney}}</span></div>
						<div class="company-claim"><span v-for="itemClaim in item.companyMessage.companyClaim">{{itemClaim}}</span><span class="company-invite">{{item.companyMessage.recruitMessage}}</span></div>
						<div class="company-welfare"><span v-for="itemWelfare in item.companyMessage.companyClaim">{{itemWelfare}}</span></div>
						<p class="company-name"><img class="company-logo" :src="item.logo" alt=""><div class="company-text">{{item.companyName}}</div><i></i><span></span></p>
					</router-link>
			</ul>
		</div>

	</div>
</template>
<script type="text/ecmascript-6">
	import slideShow from '../slide-show/slide-show.vue'
	export default {
		name: 'zhiWei',
		components: {
			slideShow
		},
		data() {
			return {
				data: null,
				invTime: 4000,
				slides: [
					{
						src: require('../../static/img/1.jpg'),
						title: 'xxx1'
					},
					{
						src: require('../../static/img/2.jpg'),
						title: 'xxx2'
					},
					{
						src: require('../../static/img/3.jpg'),
						title: 'xxx3'
					}
				]
			}
		},
		computed: {
		},
		methods: {
			},
		beforeCreate() {
			this.$http.get("./static/data.json").then(function(res){
				this.data =  (JSON.parse(res.bodyText)).zhiWei.companies;
			})
		},
		mounted() {
		}
	}
</script>

<style scoped>
.zhiWei{
	height: 100%;
}
.header{
	overflow: hidden;
}
.banner{
	width: 100%;
	height: 185px;
	position: relative;
}
.banner img{
	display: block;
	height: 185px;
}
.banner-item{
	z-index: 10;
	transition: all 3s;
	position: absolute;
	top: 0;
	width: 100%;
	height: 185px;
}
.hideItem{
	z-index: -10;
	visibility: hidden;
}
.input-search{
	width: 80%;
	height: 34px;
	left: 13px;
	text-align: center;
	top: 32px;
	background: #ededed;
	border: none;
	position: absolute;
	z-index: 20;
	border-radius: 20px;
	font-size: 14px;
}
.saoYiao{
	position: absolute;
	top: 36px;
	right: 13px;
	z-index: 20;
	width: 20px;
	height: 20px;
	background: url(../../static/img/saoYisao.png) no-repeat;
	background-size: 20px 20px;
}
.saoYiao:after{
	content: '扫一扫';
	top:110%;
	left: -20%;
	position: absolute;
	width: 160%;
	display: block;
	color:white;
	font-size: 2px;
	font-weight: 200;
}
.school-zhaoPin{
	display: flex;
	flex-direction: row;
	padding: 10px 0;
}
.school-zhaoPin>.content{
	flex:1;
	padding-left:15px;
	position: relative;
}
.school-zhaoPin .content>div{
	float: left;
}
.school-zhaoPin>.border-1px-right:before{
	position: absolute;
	content: '';
	display: block;
	height: 100%;
	right: 0;
	border-right: 1px solid rgb(219, 219, 219);
}
@media (-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio:2){
	.border-1px-right:before{
		transformX:scale(0.5);
	}
}
	.school-title{
		font-size: 16px;
		color: #2e2e2e;
		font-weight: 400;
	}
	.school-message{
		padding-top: 10px;
		padding-left: 10px;;
	}
	.school-remarks{
		font-size: 12px;
		color: #7f7f7f;
	}
.main{
	background-color: #f8f8fa;
	height: 100%;
}
	.company-message{
		width: 100%;
		padding: 10px 0 40px 0;
	}
	.company-message .company-content{
		padding: 12px 15px;
		margin-bottom:10px;
		background-color: #fff;
		overflow: hidden;
	}
	.company-title{
		line-height: 16px;
		font-size: 16px;
		font-weight: 700;
	}
	.company-title .company-money{
		color: red;
		float: right;
	}
	.company-name{
		line-height: 35px;
	}
	.company-logo{
		width: 35px;
		height: 35px;
		float: left;
	}
	.company-text{
		float: left;
		height: 35px;
		line-height: 35px;
		margin-left: 10px;
	}
	.company-claim, .company-welfare{
		color: #b7b7b7;
		font-size: 12px;
	}
	.company-claim {
		margin-top: 10px;
	}
	.company-claim>span{
		margin-right: 10px;;
	}
	.company-claim .company-invite{
		float: right;
	}
	.company-welfare{
		margin-top: 14px;
		margin-bottom: 17px;
	}
	.company-welfare span{
		background-color: #f2f8fb;
		margin-right: 12px;
		display: inline-block;
		padding: 5px;
	}
</style>



